import React, { useContext } from 'react'

import { GOrderList, OrderStateCategoryEnum } from '../../../graph/types'

export type TOrder = GOrderList.Orders

export type TCategorizedOrders = { [state in OrderStateCategoryEnum]: TOrder[] }

export interface IOrderListContext {
  isLoading: boolean
  noFilter: boolean
  orders?: ReadonlyArray<TOrder>
  categorizedOrders?: TCategorizedOrders
  categories?: ReadonlyArray<GOrderList.OrderStateCategories>
}

const OrderListContext = React.createContext<IOrderListContext>({
  isLoading: false,
  noFilter: false,
})

export function useOrderList() {
  return useContext(OrderListContext)
}

export default OrderListContext
