import { Trans } from '@lingui/react'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { CardListTitle } from '../../../atoms/CardListTitle'
import { Loading } from '../../../atoms/Loading'
import { useRoot } from '../../../atoms/RootProvider'
import { useOrderList } from '../context/OrderListContext'
import { OrderListCategory } from '../molecules/OrderListCategory'

export const OrderList: React.FC = () => {
  const { order: orderModel } = useRoot()
  const {
    isLoading,
    noFilter,
    orders,
    categories,
    categorizedOrders,
  } = useOrderList()

  if (isLoading) {
    return <Loading />
  }

  if (noFilter) {
    return (
      <CardListTitle>
        <Trans>Select at least one branch</Trans>
      </CardListTitle>
    )
  }

  if (orders!.length === 0) {
    return (
      <CardListTitle>
        <Trans>No orders</Trans>
      </CardListTitle>
    )
  }

  return useObserver(() => (
    <>
      {categories!.map(category => (
        <OrderListCategory
          key={category.enum}
          isCategoryCollapsed={orderModel.list.isCategoryCollapsed(
            category.enum,
          )}
          category={category}
          orders={categorizedOrders![category.enum] || []}
          showBranches={orderModel.list.branches.length > 1}
          toggleCategoryCollapse={orderModel.list.toggleCategoryCollapse}
          toggleOrderDetail={orderModel.list.toggleOrderDetail}
          isOrderDetailOpen={orderModel.list.isOrderDetailOpen}
        />
      ))}
    </>
  ))
}
