import gql from 'graphql-tag'

import {
  OrderMapDeliveryFragment,
  OrderMapDeviceFragment,
  OrderMapDriverFragment,
} from '../organisms/OrderMap.graphql'

export const OrderMapDeliveriesQuery = gql`
  query QOrderMapDeliveries($branches: [ID!]!) {
    deliveries: drivers(companyBranches: $branches, onlyAvailable: false) {
      ...GOrderMapDelivery
    }
  }
  ${OrderMapDeliveryFragment}
`

export const OrderMapDeliveriesSubscription = gql`
  subscription SOrderMapDeliveries($branches: [ID!]!) {
    update: updatedTransportationDeviceUser(companyBranches: $branches) {
      actionType
      device: transportationDeviceUser {
        ...GOrderMapDevice
        user {
          ...GOrderMapDriver
        }
      }
    }
  }
  ${OrderMapDeviceFragment}
  ${OrderMapDriverFragment}
`
