import React, { useEffect, useState } from 'react'
import { createPortal } from 'react-dom'

import { useGoogleMap } from './BaseMap'

interface IProps {
  position: keyof typeof google.maps.ControlPosition
}

export const MapControl: React.FC<IProps> = ({ position, children }) => {
  const map = useGoogleMap()
  const [container] = useState(() => document.createElement('div'))
  useEffect(
    () => {
      const controls = map.controls[google.maps.ControlPosition[position]]
      controls.push(container)
      return () => {
        const index = controls.getArray().findIndex(node => node === container)
        controls.removeAt(index)
      }
    },
    [position],
  )
  return createPortal(children, container)
}
