import gql from 'graphql-tag'

import { GeoPointFragment } from '../../../graph/fragments'

export const OrderMarkerFragment = gql`
  fragment GOrderMarker on Order {
    id
    deliverAt
    gps {
      ...GGeoPoint
    }
  }
  ${GeoPointFragment}
`

export const OrderMapDeviceFragment = gql`
  fragment GOrderMapDevice on TransportationDeviceUser {
    id
    lastPosition {
      gps: geoPosition {
        ...GGeoPoint
      }
      timestamp
    }
  }
  ${GeoPointFragment}
`
export const OrderMapDriverFragment = gql`
  fragment GOrderMapDriver on User {
    id
    initials
    countUndeliveredOrders
  }
`

export const OrderMapDeliveryFragment = gql`
  fragment GOrderMapDelivery on User {
    ...GOrderMapDriver
    device: usedTransportationDevice {
      ...GOrderMapDevice
    }
  }
  ${OrderMapDriverFragment}
  ${OrderMapDeviceFragment}
`

export const OrderMapActiveDriverFragment = gql`
  fragment GOrderMapActiveDriver on User {
    id
    firstName
    lastName
    countUndeliveredOrders
    userDeliveringOrders(onlyUndelivered: true) {
      id
      order {
        id
      }
    }
  }
`
