import { CircularProgress } from '@material-ui/core'
import { Check, Close, WarningOutlined } from '@material-ui/icons'
import faker from 'faker'
import { autorun } from 'mobx'
import { useDisposable } from 'mobx-react-lite'
import React, { useState } from 'react'
import styled from 'styled-components'

import { useRoot } from '../../../atoms/RootProvider'
import { GOrderVam } from '../../../graph/types'
import { useExecutableQuery } from '../../../hooks/useExecutableQuery'
import { Colors } from '../../../styling'
import { OrderVamQuery } from './OrderFormCustomerLookup.graphql'

const ErrorIcon = styled(WarningOutlined)`
  font-size: 1rem;
  color: ${Colors.orange};
` as typeof WarningOutlined

const NotFoundIcon = styled(Close)`
  font-size: 1rem;
  color: ${Colors.greyMiddle};
` as typeof Close

const FoundIcon = styled(Check)`
  font-size: 1rem;
  color: ${Colors.green};
` as typeof Check

export const OrderFormCustomerLookup: React.FC = () => {
  const { createOrder } = useRoot()
  const [statusIcon, setStatus] = useState<ReactNode>(null)

  const { execute } = useExecutableQuery<GOrderVam.Query, GOrderVam.Variables>(
    OrderVamQuery,
    { fetchPolicy: 'no-cache' },
  )

  useDisposable(() =>
    autorun(() => {
      executeQuery(createOrder.currentBranch.id)
    }),
  )

  async function executeQuery(branchId: ID) {
    createOrder.updateFromVam(null)

    if (!isPhoneValid(createOrder.phone, createOrder.phonePrefix)) {
      return setStatus(null)
    }

    setStatus(<CircularProgress size={20} />)

    const result = await execute({
      branchId,
      phone: `${createOrder.phonePrefix}${createOrder.phone}`,
    })

    if (result.hasAnyError) {
      return setStatus(<ErrorIcon />)
    }

    if (result.data) {
      const { vamCustomer } = result.data
      if (vamCustomer.isFound) {
        createOrder.updateFromVam(vamCustomer)
        return setStatus(<FoundIcon />)
      }
    }

    setStatus(<NotFoundIcon />)
  }

  return statusIcon
}

function isPhoneValid(phone: string, phonePrefix: string) {
  return Boolean(
    phone && phone.length >= 6 && phonePrefix && phonePrefix.length >= 3,
  )
}

// @ts-ignore Can be useful when there is an error in DEV
function getFakeVam(): GOrderVam.VamInformation {
  return {
    firstName: 'FAKE',
    lastName: faker.name.lastName(),
    email: faker.internet.email(),
    lastOrder: {
      note: faker.lorem.sentence().slice(0, 50 - 1),
    },
    isFound: true,
    locations: [],
  }
}
