import React from 'react'
import { OverlayView } from 'react-google-maps'

import { GOrderMarker } from '../../../graph/types'
import { gpsToLatLng } from '../../../helpers/gps'
import { DeliveryTimeBadge } from './DeliveryTimeBadge'

interface IProps {
  order: GOrderMarker.Fragment
  onClick(): void
  isFade?: boolean
}

export const OrderMapMarker: React.SFC<IProps> = ({ order, isFade, onClick }) =>
  order.gps ? (
    <OverlayView
      position={gpsToLatLng(order.gps)}
      mapPaneName={OverlayView.OVERLAY_MOUSE_TARGET}
    >
      <DeliveryTimeBadge
        deliverAt={order.deliverAt}
        deliveredAt={null}
        onClick={onClick}
      />
    </OverlayView>
  ) : null
