import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { DeliveryTypeEnum, OrderStateCategoryEnum } from '../../../graph/types'
import { OrderMapMarker } from '../atoms/OrderMapMarker'
import { useOrderList } from '../context/OrderListContext'

const categoriesOnMap = [
  OrderStateCategoryEnum.NewOrders,
  OrderStateCategoryEnum.Preparing,
  OrderStateCategoryEnum.OnWay,
]

export const OrderMapOrders: React.FC = () => {
  const { order: orderModel } = useRoot()
  const { isLoading, orders } = useOrderList()
  if (isLoading || !orders) {
    return null
  }
  return (
    <>
      {orders
        .filter(order => categoriesOnMap.includes(order.orderStateCategory))
        .filter(
          order =>
            order.deliveryType &&
            order.deliveryType.enum === DeliveryTypeEnum.Messenger,
        )
        .map(order => (
          <OrderMapMarker
            key={order.id}
            order={order}
            onClick={() => orderModel.list.focusOrderDetail(order.id)}
          />
        ))}
    </>
  )
}
