import { RingVolume } from '@material-ui/icons'
import { LocationDescriptor } from 'history'
import React from 'react'
import { useSubscription } from 'react-apollo-hooks'
import { Link } from 'react-router-dom'

import { TouchButtonBadge } from '../../../atoms/TouchButtonBadge'
import { SOrderCall } from '../../../graph/types'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'
import { OrderCallSubscription } from '../organisms/OrderCallWatch.graphql'

interface INewOrderButtonProps {
  showOnlyText?: boolean
}

export const CreateOrderButton: React.FC<INewOrderButtonProps> = ({
  showOnlyText = false,
}) => {
  const { data } = useSubscription<
    SOrderCall.Subscription,
    SOrderCall.Variables
  >(OrderCallSubscription)

  const location: LocationDescriptor = {
    pathname: '/order/create',
    state: data ? data.call : null,
  }

  return (
    <Link to={location}>
      <TouchButton
        text={i18n.t`Create order`}
        iconName={showOnlyText ? undefined : 'add_circle'}
        color={Colors.turquoise}
        badge={
          data && data.call && data.call.isRinging ? (
            <TouchButtonBadge>
              <RingVolume />
            </TouchButtonBadge>
          ) : null
        }
      />
    </Link>
  )
}
