import React from 'react'
import { useQuery, useSubscription } from 'react-apollo-hooks'

import { useGoogleMap } from '../../../atoms/BaseMap'
import {
  GOrderMapDelivery,
  QOrderMapDeliveries,
  SOrderMapDeliveries,
} from '../../../graph/types'
import { gpsToLatLng } from '../../../helpers/gps'
import { OrderMapDelivery } from '../atoms/OrderMapDelivery'
import {
  OrderMapDeliveriesQuery,
  OrderMapDeliveriesSubscription,
} from './OrderMapDeliveries.graphql'

interface IProps {
  branches: ReadonlyArray<ID>
}

export const OrderMapDeliveries: React.FC<IProps> = ({ branches }) => {
  const { data, updateQuery } = useQuery<
    QOrderMapDeliveries.Query,
    QOrderMapDeliveries.Variables
  >(OrderMapDeliveriesQuery, { variables: { branches }, suspend: false })

  useSubscription<
    SOrderMapDeliveries.Subscription,
    SOrderMapDeliveries.Variables
  >(OrderMapDeliveriesSubscription, {
    variables: { branches },
    onSubscriptionData({ subscriptionData: { data: dataUpdate } }) {
      if (!dataUpdate) {
        return
      }
      const {
        update: { device },
      } = dataUpdate
      if (!device) {
        return
      }
      updateQuery(prev => {
        let driverUpdated = false
        // map through current driver and try to find the updated one
        const deliveries = prev.deliveries.map(driver => {
          if (driver.device && driver.device.id === device.id) {
            if (driver.id === device.user.id) {
              driverUpdated = true
              return {
                ...driver,
                device,
              }
            } else {
              // driver got assigned different device
              // remove it from here to add it later
              return null
            }
          }
          return driver
        })
        if (!driverUpdated) {
          const {
            user: { __typename, id, initials, countUndeliveredOrders },
            ...userDevice
          } = device
          deliveries.push({
            __typename,
            id,
            initials,
            countUndeliveredOrders,
            device: userDevice,
          })
        }
        return {
          ...prev,
          deliveries: deliveries.filter(
            Boolean,
          ) as GOrderMapDelivery.Fragment[],
        }
      })
    },
  })

  const map = useGoogleMap()

  if (!data || !data.deliveries || data.deliveries.length === 0) {
    return null
  }

  const mapDeliveries = data.deliveries
    .filter(driver => Boolean(driver.device && driver.device.lastPosition))
    .map(driver => (
      <OrderMapDelivery
        key={driver.id}
        position={driver.device!.lastPosition!.gps}
        label={driver.initials || driver.id}
        isEmpty={driver.countUndeliveredOrders === 0}
        onClick={() => {
          if (map) {
            map.panTo(gpsToLatLng(driver.device!.lastPosition!.gps))
          }
        }}
      />
    ))

  return <>{mapDeliveries}</>
}
