import { useDisposable, useObserver } from 'mobx-react-lite'
import React from 'react'

import { BaseMap } from '../../../atoms/BaseMap'
import { useRoot } from '../../../atoms/RootProvider'
import { OrderMapDeliveries } from '../molecules/OrderMapDeliveries'
import { OrderMapOrders } from '../molecules/OrderMapOrders'

export const OrderMap: React.FC = () => {
  const { order: orderModel } = useRoot()

  useDisposable(() =>
    orderModel.list.observerOrderDetail(change => {
      if (change.type === 'add') {
        orderModel.map.panToOrder(change.newValue)
      }
    }),
  )

  return useObserver(() => (
    <BaseMap model={orderModel.map}>
      <OrderMapOrders />
      <OrderMapDeliveries branches={orderModel.list.branchesFilter} />
    </BaseMap>
  ))
}
